import React from 'react';
import {Image,Row,Col}from 'react-bootstrap';
import '../css/about.css';
import DogPaw from '../Images/2paws.png';
import Dog from '../Images/about.jpg';
import Inspire from '../Images/inspire.jpg';

import Footer from './Footer';
import Members from './members';

const About = () => {
    return(
        <div>
        <div className='about-container'>
            <Row>
                <Col>
                    <Image src={DogPaw} className='about-dogpaw' />
                    <p className='about-head'>About Us</p>
                    <p className='about-content'>We are the group of people who love dogs. Everyday we see so many stray dogs who never get enough food, 
                       are never cared for and are on the verge of death either because of an accident or because of a disease.
                       So we started this community with the goal of providing the best service we can to these dogs.</p>
                </Col>
                <Col>
                    <Image src={Dog} className='about-image' />
                </Col>
            </Row>
        </div>
        <div className='inspire-container'>
            <Row>
                <Col>
                    <Image src={Inspire} className='about-image' />
                </Col>
                <Col>
                    <Image src={DogPaw} className='about-dogpaw' />
                    <p className='about-head'>Our Inspiration</p>
                    <p className='about-content'>This goes to the time when we found a stray female dog who was hit on her head severly. 
                    She was bleeding like hell so we took her in our care. Initially we did the best we can on our own level, we cleaned 
                    her wounds, applied ointments and covered the wounds. Then we took her to Veteran doctor and got her a good treatment.
                    We bought dog food and fed it to her. It wasn't an easy task as she was a stray dog and would sometimes become offensive,
                    but in the end she is healthy than before. She found some good friends in us and we in her.
                    </p>
                    <p className='inspire-note'><spam className='inspire-note-head'>Note:- </spam> We have a small request for everyone. Please don't be abusive with animals. We believe we are 
                    also animals but since we are more intellegent we call ourselves Human. If we really are Humans then it is our duty to
                    give respect, love, and freedom to animals too. Be kind to animals, then only they'll be kind to you.</p>
                </Col>
            </Row>
            </div>
            <Members/>          
            <Footer/>
        </div>
    );
};

export default About;