import React from 'react';
import {ListGroup} from 'react-bootstrap';
import '../css/contribute.css';

import Footer from './Footer';

const Contribute = () => {
    return(
        <div className='contribute-container'>
            <div className='contribute-div'>
            <h2 className='contribute-head'>Here are some ways, you can contribute to our community or be a part of it!</h2>
            <ListGroup style={{ paddingTop: '20px'}}>
                <ListGroup.Item><spam className='list-head'>Donations:-</spam>You can either donate money, food or anything which you think will be usefull to dogs.</ListGroup.Item>
                <ListGroup.Item><spam className='list-head'>Feeding:-</spam>You can feed the dogs yourself and share your story with us on instagram, we will tag you in our stories and posts.</ListGroup.Item>
                <ListGroup.Item><spam className='list-head'>Contact Us:-</spam>When ever you find a stray dog who needs medical help either give us a call, DM us on instagram or drop a mail and we will try our best to help him.</ListGroup.Item>
            </ListGroup>
            </div><br/>

            <div className='contribute-div'>
                <h2 className='contribute-head'>Want to donate money?</h2>
                <p className='contribute-text'>If you live in India then you can use GPay, PhonePay or Paytm to donate money to the following number.</p>
                <ListGroup>
                <ListGroup.Item>+91 8699721219</ListGroup.Item>
                </ListGroup><br/>

                <p className='contribute-text'>If you live outside India then you can use PayPal to donate money to the following PayPal account.</p>
                <ListGroup>
                <ListGroup.Item>PayPal.Me Account:- paypal.me/karanDK7</ListGroup.Item>
                <ListGroup.Item>PayPal username:- @karanDK7</ListGroup.Item>
                </ListGroup>
            </div>
            <Footer/>
        </div>
    );
};

export default Contribute;