import React from 'react';
import {Image, Row, Col} from 'react-bootstrap';

import Footer from './Footer';
import Character from './Character';

import '../css/home.css';


import Dog1 from '../Images/dog1.png'; 

const Home = () => {
    return(
        <div className='home-container'>
            <Row>
                <Col>
                    <h2 className='home-h2'>We Care For The Dogs.</h2>
                    <p className='home-p'>We always try to provide the Dogs the Best Services.</p>
                </Col>
                <Col>
                    <Image src={Dog1} className='home-dog1' />
                </Col>
            </Row><br/>         

            <Character/> 
            <Footer/>
            
        </div>
    );
};

export default Home;