import React from 'react';

import {Row, Col, Card,Button} from 'react-bootstrap'; 
import '../css/members.css';

import Karan from '../Images/members/karan.jpg';
import Rishabh from '../Images/members/rishabh.jpg';
import Abhishek from '../Images/members/abhishek.jpg';
import Sambhav from '../Images/members/sambhav.jpg';
import Varun from '../Images/members/varun.jpg';
import Shreshth from '../Images/members/shreshth.jpg';

const Members = () =>{
    return(
        <div className='members-container'>
            {/* Founding Members */}
            <h2 className='members-head'>Members</h2>

            <Row className='members-row'>
                <Col>
                <Card className='members-card'>
                    <Card.Img variant="top" src={Karan} style={{ height: '250px' }} />
                    <Card.Body>
                        <Card.Title>Karan Singh Saini</Card.Title>
                        <Card.Text>
                        "Until one has loved an animal, a part of one's soul remains unawakened." 
                        - Anatole France
                        </Card.Text>
                        <Button href='https://www.instagram.com/karan_kss_/' variant="primary">Follow on Instagram</Button>
                    </Card.Body>
                </Card>
                </Col>
                <Col>
                <Card className='members-card'>
                    <Card.Img variant="top" src={Rishabh} style={{ height: '250px' }}  />
                    <Card.Body>
                        <Card.Title>Rishabh Singh</Card.Title>
                        <Card.Text>
                        “A dog is the only thing on earth that loves you more than he loves himself.”
                        ― Josh Billings
                        </Card.Text>
                        <Button href='https://www.instagram.com/rishabh.katheria/' variant="primary">Follow on Instagram</Button>
                    </Card.Body>
                </Card>
                </Col>
                <Col>
                <Card className='members-card'>
                    <Card.Img variant="top" src={Sambhav} style={{ height: '250px' }} />
                    <Card.Body>
                        <Card.Title>Sambhav Jain</Card.Title>
                        <Card.Text>
                        “You can judge a man's true character by the way he treats his fellow animals.”
                        ― Paul McCartney
                        </Card.Text>
                        <Button href='https://www.instagram.com/sambhav_04.09/' variant="primary">Follow on Instagram</Button>
                    </Card.Body>
                </Card>
                </Col>
            </Row><br/>

            <h2 className='members-head'>Volunteers</h2>

            <Row className='members-row'>
                <Col>
                <Card className='members-card'>
                    <Card.Img variant="top" src={Varun} style={{ height: '250px' }}  />
                    <Card.Body>
                        <Card.Title>Varun</Card.Title>
                        <Card.Text>
                        "The question is not, 'Can they reason?' nor 'Can they talk?' but 'Can they suffer?" 
                        ― Jeremy Bentham
                        </Card.Text>
                        <Button href='' variant="primary">Follow on Instagram</Button>
                    </Card.Body>
                </Card>
                </Col>
                <Col>
                <Card className='members-card'>
                    <Card.Img variant="top" src={Shreshth} style={{ height: '250px' }}  />
                    <Card.Body>
                        <Card.Title>Shreshth</Card.Title>
                        <Card.Text>
                        "Animals are such agreeable friends ― they ask no questions, they pass no criticisms." 
                        ― George Eliot
                        </Card.Text>
                        <Button href='https://www.instagram.com/shreshthgautam75/' variant="primary">Follow on Instagram</Button>
                    </Card.Body>
                </Card>
                </Col>
                <Col>
                <Card className='members-card'>
                    <Card.Img variant="top" src={Abhishek} style={{ height: '250px' }}  />
                    <Card.Body>
                        <Card.Title>Abhishek</Card.Title>
                        <Card.Text>
                        “If there are no dogs in Heaven, then when I die I want to go where they went.”
                        ― Will Rogers
                        </Card.Text>
                        <Button href='https://www.instagram.com/abhi_ang/' variant="primary">Follow on Instagram</Button>
                    </Card.Body>
                </Card>
                </Col>
            </Row>
        </div>
    );
};

export default Members;