import React from 'react';
import {Image, Accordion,Button,Carousel} from 'react-bootstrap';

import Blog1_1 from '../Images/blogs/park1.jpeg';
import Blog1_2 from '../Images/blogs/park2.png';
import Blog1_3 from '../Images/blogs/park3.png';
import Footer from './Footer';
import '../css/blogs.css';

const Blogs = () => {
    return(
        <div className='blogs-container'>
            {/* Blog 1 */}
            <Accordion>
            <div className='blogs-div'>
                <Carousel>
                    <Carousel.Item>
                        <img className="blog-photo" src={Blog1_1} alt="First slide"/>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img className="blog-photo" src={Blog1_2} alt="First slide"/>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img className="blog-photo" src={Blog1_3} alt="First slide"/>
                    </Carousel.Item>
                </Carousel>
                <h5 className='blog-date'>4<sup>th</sup> October, 2020</h5>
                <h1 className='blog-head'>Saved a Puppy from an Old Man's Beating</h1>
                <p className='blog-content'>
                    <span>It was just another morning when me and Karan were ready to go for our morning exercise. We were outside Karan's house when we heard squeqy voice like crying. On taking a look we saw an old man using a stick to push something away. We gently went towards him and saw that this old man was using his stick to push a little puppy away from his house. On asking he said that he don't want this creature to live and grow in front of his house as everywhere there will be litters and smell.
                    We asked him not to use his stick and be gentle, but he replied that the dog is filthy and he doesn't want to make his hands dirty by touching him.</span><br/><br/> 
                    {/* Collapse the rest of the blog */}
                    <Accordion.Toggle as={Button} variant="link" eventKey="0">
                        Read More!
                    </Accordion.Toggle>

                    <Accordion.Collapse eventKey="0">
                        <p>
                        <span>Karan actually knew where this puppy came from and asked the old man to move aside. He gently picked up the sweet creature caressing his head. The puppy moved a little bit but settled down later in the warm hands. We took the puppy to the park where he came from. We went into the park and as soon as we put the puppy on the ground, this little ceature started moving his tail and looked straight and started running. He was really active and alive now. </span><br/><br/> 
                        <span>We guessed he was running towards his home and having this thought we followed him. This puppy on reaching the corner of the park went inside the pit digged by its mother. 
                        On peeking inside we saw there we two more puppies inside the pit scared and whimpering. It was hot outside so we went straight away to nearby shop and bought a packeet of lassi . We poured it completey in the utensile that was already there.
                        Puppies were little scared initially but later they came out of the pit and started drinking from the utensile. This was a heart warming view and we were really happy to rescue one of those sweet creature.</span></p>
                    </Accordion.Collapse>
                </p>
                <span className='blog-footer'>Blog By:- Rishabh Singh</span>
            </div>
            </Accordion><br/>

            <Footer/>
        </div>
    );
};


export default Blogs;